import React from 'react'
import './Header.css'
import Typed from "react-typed"
import VideoBg from "reactjs-videobg";
// import Particles from 'react-particles-js';
// import Nav from './Nav';
import blockChain from './Video/video1.mp4'


const Header = () => {
    return (
        <div className="body">
            <VideoBg >
            <VideoBg.Source src={blockChain} type="video/mp4" />
            </VideoBg>;
        <div className="profile-container">
            <div className="profile-details-role">
                <span>
                        {""}
                        <h2>
                            {""}
                            <Typed
                            strings={["We Develop Custom Application Softwares", "Blockchain Technology and Decentralized Applications", "Web Based Applications. (websites)","Mobile Applications","Machine Learning and Artificial Intelligence Solutions"]}
                            typeSpeed={40}
                            backSpeed={60}
                            loop
                            />
                        </h2>
                        <span className="profile-role-tagline">
                        In today's world, your website is your 24hr office where you first interact with your clients. We are here<br/> to help you
                         design, build, test,deploy and maintain custom application software as per your specifications<br/>
                         We anchor on modern technology (AI and Blockchain) to deliver our promise. Let us work with you!     
                        </span>
                    </span>
                    {/* <Particles id="particles-js"
                        params={{
                            "particles": {
                                "number": {
                                    "value": 50
                                },
                                "size": {
                                    "value": 3
                                }
                            },
                            "interactivity": {
                                "events": {
                                    "onhover": {
                                        "enable": true,
                                        "mode": "repulse"
                                    }
                                }
                            }
                        }} /> */}

                    </div>
        </div>
    </div>
    )
}

export default Header
