import { Parallax } from 'react-parallax';
import Choose from './Choose';
import Block from '../img/p1.jpeg'
import './ImageOne.css'

const ImageOne = () => (
    <div className="image">
    <Parallax className="image" bgImage={Block} strength={800}>
        <Choose/>
    </Parallax>
    </div>
);

export default ImageOne