import React,{useState} from 'react'
import './Reason.css'

const notes =[
    {
        id: '1',
        img: "",
        header: "Transparency",
        details: "Immediately you start a project with us, you gain access to a dashboard where you can see the entire architecture of your software, systems involved, languages used, tasks complete and a live view of the software. You will have a dedicated project manager with whom you can discuss your opinions and get them integrated into the software. We also report to you in 2 weeks intervals or as deemed appropriate.",
    },
    {
        id: '2',
        img: "",
        header: "Security",
        details: "Ace always works hard to ensure our customers' systems are safe from hacks and other security threats. We patch our clients' software's regularly, train the software users on best practices, we automate day-to-day security tasks such as analyzing firewall changes and device security configurations to allow security staff to focus on more strategic security initiatives, among other measures. You are safe with us!",
    
    },
    {
        id: '3',
        img: "",
        header: "Trust",
        details: "Being a strong believer and enthusiast of blockchain technology, we acknowledge the importance of Trust in any transaction. We use stable proven technologies and our software engineering process adopts the test-driven methodology which ensures stability and reliability of the software, thus trust that the software will work. We DO NOT hide charges in our transactions, our word is our bond, we deliver our promise!",
        
    }
];
const Reason = () => {
    const [cards, setCards] = useState(notes);
    return (
        <section className="about">
           {cards.map((project)=>{
               return(
                <Names key={notes.id} {...project}/>
               );
           })}
        </section>
    );
}
const Names =(props)=>{
    const {id,img,header,details} = props;
    return(
             <div className="aboutus" key={id}>
                        <h2>{header}</h2>
                        <p>{details}</p>
            </div>
    );
    }

export default Reason
