import React from 'react'
import Custom from './Custom'
import './Custom.css'

const CustomFetch = () => {
    const {data,loading,error,refetch} = Custom("https:v2.jokeapi.dev/joke/Any");
    if(loading) return <h2>Loading...</h2>
    if(error) console.log(error);

    return (
        <div className="custom">
            <h1>Fun moments</h1>
            <h2>{data?.setup} : {data?.delivery}</h2>
            <button className="btn1" onClick={refetch}>New Joke</button>
        </div>
    )
}

export default CustomFetch
