import React,{useState} from 'react'
import './Mail.css'

const Mail = () => {
    const [email,setEmail] = useState("");
    const onChange =(e)=>{
        setEmail(e.target.value);
    }
    return (
        <div className="email">
            <p>We only send Job vacancies, weekly industry updates, insider perspectives and our service offers</p>
            <input type="email" value={email} onChange={onChange} size="40"/>
            <button className="btn" >subscribe</button>
        </div>
    )
}

export default Mail
