import React,{useState} from 'react'
import './PropCode.css'
const notes =[
    {
        id: '1',
        img: "data:image/jpeg;base64,",
        header: "Blockchain Solutions",
        details: "Blockchain solutions are not only limited to the exchange of cryptocurrencies.There are numerous benefits that this technology can present to your businesses regardless of the industry you are in.We believe that distributed ledger (DLT) technology carries the potential to...",
        link: 'Read More',
    },
    {
        id: '2',
        img: "data:image/jpeg;base64,",
        header: "Web App Development",
        details: "Your website is your first contact with your customers and your 24/7 online office. Having a good looking website is equally important as having one that functions properly to serve your specific needs. Ace acknowledges the fact that businesses have unique requirements and...",
        link: 'Read More',
    },
    {
        id: '3',
        img: "https://media.istockphoto.com/photos/circuit-board-picture-id1145585734?k=20&m=1145585734&s=612x612&w=0&h=BWthKyWj7tFIH_hixXUXW-Gqo5Q2gxFoYCj6XSUNB2A=",
        header: "Machine Learning & AI",
        details: "Artificial Intelligence (AI) is the science of making machines (computers) smarter in such a way that they can analyze situations and make decisions on their own, independent of human beings. The process by which machines acquire the necessary knowledge on which they base...",
        link: 'Read more',
    }
];

const PropCode = () => {
    const [cards, setCards] = useState(notes);
    return (
        <div className="cont">
        <div className="content">
           {cards.map((project)=>{
               return(
                <Names key={notes.id} {...project}/>
               );
           })}
        </div>
        </div>
    );
}
const Names =(props)=>{
const {id,img,header,details,link} = props;
return(
         <div className="project1" key={id}>
                    <img src={img} alt="image" width="30%" height="20%" />
                    <h1>{header}</h1>
                    <p>{details}</p>
                    <button className="btn1">{link}</button>
        </div>
);
}
export default PropCode
